import React, { useEffect, useState } from "react";
import axios from "axios";
import styled from "styled-components";
import { ModalButton } from "./Main";
import { Link, useNavigate } from "react-router-dom";
import { Line } from "react-chartjs-2";
import Select from "react-select";
import {
  registerables,
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
} from "chart.js";

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
  ...registerables
);

const DivDashboard = styled.div`
  width: 80vw;
  height: 80vh;
  background-color: white;
`;

const DivGraph = styled.div`
  width: 70vw;
  height: 80vh;
`;

const API_URL = process.env.REACT_APP_URL;

const Dashboard = () => {
  const navigate = useNavigate();
  const [data, setData] = useState(null);
  const [month, setMonth] = useState("06"); //  6 
  const year = "2024"; //   

  //      
  const filteredData = data?.filter((item) => {
    const itemDate = new Date(item.date);
    return (
      itemDate.getFullYear() === parseInt(year) &&
      (itemDate.getMonth() + 1).toString().padStart(2, "0") === month
    );
  });

  //   
  const chartData = {
    labels: filteredData?.map((item) => item.date.split("-")[2]), // 
    datasets: [
      {
        label: `${year}-${month}`,
        data: filteredData?.map((item) => item.number),
        fill: false,
        borderColor: "rgb(75, 192, 192)",
        tension: 0.1,
      },
    ],
  };

  //   
  const chartOptions = {
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  //  
  const fetchData = async () => {
    try {
      const response = await axios.get(API_URL);
      setData(response.data);
      console.log("Data fetched successfully:", response.data);
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  //      
  useEffect(() => {
    fetchData();
  }, []);

  //   
  const options = [
    { value: "06", label: "6" },
    { value: "07", label: "7" },
    { value: "08", label: "8" },
    { value: "09", label: "9" },
    { value: "10", label: "10" },
    { value: "11", label: "11" },
    { value: "12", label: "12" },
  ];

  return (
    <div
      style={{
        height: "100vh",
        width: "100vw",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <DivDashboard className="Modal">
        <div>
          {/*     */}
          <Select
            options={options}
            value={options.find((option) => option.value === month)}
            onChange={(selectedOption) => setMonth(selectedOption.value)}
          />
        </div>
        {/*   */}
        <DivGraph>
          <Line data={chartData} options={chartOptions} />
        </DivGraph>
        {/*    */}
        <ModalButton onClick={() => navigate("/")}> </ModalButton>
      </DivDashboard>
    </div>
  );
};

export default Dashboard;
